.card-coordinators {
    width: 45%; 
    border: solid 3px #036588 !important;
    margin-top: 30px;
    margin-left: 50px;
    position: relative;
    border-radius: 30px  !important;
    padding: 5px;
    box-shadow: 0px 1px 10px #2eaddb;
    background-image: url('../web_design/card-bg.jpg'); 
    background-repeat: no-repeat;
    background-size: cover;
}

.icon{
border-radius: 100% !important;
background-color: green !important;
}

.rounded-rect {
    border-radius:100%;
  border: 2px solid #036588;
  width: 150px;
  height: 150px;
  box-shadow: 0px 1px 10px #535353;
 
}


.card-name {
    background: rgb(0, 140, 255);
    height: 40px;
    width: 90%;
    margin-left: 30px;
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    color:#fff;
    font-size: 20px;
    border-radius: 10px;
    /* border: solid 2px #0079A5; */

}

.card-num{
    /* background: rgb(0, 140, 255); */
    /* border-radius: 10px; */
    font-size: 1.1em;
    font-weight: 500;
    padding: 5px;
    color: #272727;
    margin-left: 50px;

}

.card-email {
    /* background: rgb(0, 140, 255); */
    /* border-radius: 10px; */
    font-size: 1.1em;
    font-weight: 500;
    padding: 5px;
    color: #272727;

    margin-left: 50px;
}

@media (max-width:720px){
    .card-coordinators{
        width:210px;
    }
    .card-image{
        display: flex;
        justify-content: center;
    }
    .card-name, .card-num, .card-email{
        margin: 15px;
        padding:0px;
    }
}